import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';

import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Toast from 'react-bootstrap/Toast';


const Contacto= () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_6tkph46', 'template_derttml', form.current, 'i5JaqjiKDWMG2t4y5')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  };

  return (
    
    <Form ref={form} onSubmit={sendEmail}>
    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
      <Form.Label>Nombre</Form.Label>
      <Form.Control type="text" name="user_name" placeholder="Nombre" />
    </Form.Group>
    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
      <Form.Label>Email</Form.Label>
      <Form.Control type="email" name="message" placeholder="Email" />
    </Form.Group>
    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1" >
      <Form.Label>Mensaje</Form.Label>
      <Form.Control as="textarea" name="message" placeholder='Mensaje' rows={8} />
    </Form.Group>
    <Button variant="primary" type="submit">
        Enviar
      </Button>
  </Form>
  
  
);
}


export default Contacto
